<template>
    <div id="presentations">
      <v-container
        fluid
        grid-list-sm
      >
        <v-layout row wrap justify-center v-for="(item, index) in presentations" :key="index">
          <v-flex xs12> 
            <v-card 
              flat
              class="custom-ripple mb-2 post-title-link"
              :ripple="{ class: 'primary--text' }"
              v-on:mouseover="addShadow($event)"
              v-on:mouseout="removeShadow($event)"
              >
                <a><v-card-text class="word" @click="handleClick(item.url)">{{ item.title }}</v-card-text></a>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </div>
</template>
<script>
export default {
  data() {
    return {
      page: 1,
      size: 10,
      presentations: [
        {
          title: "The Raft Consensus Algorithm",
          url: "https://guanpengchn.github.io/presentation/Raft.html"
        },
        {
          title: "前端生态简介",
          url: "https://guanpengchn.github.io/presentation/frontend-intro.html"
        },
        {
          title: "golang调用python",
          url: "https://guanpengchn.github.io/presentation/golang-python.html"
        }
      ],
      isProgress: false
    };
  },
  methods: {
    handleClick: function(url) {
      window.open(url, "_blank");
    },
    addShadow: function($event) {
      $event.currentTarget.className += " elevation-3"
    },
    removeShadow: function($event) {
      $event.currentTarget.className = $event.currentTarget.className.replace(/ elevation-3/g," ")
    }
  }
};
</script>
